#about-us-main {
  border: var(--third-color) solid 5px;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100vh;
  padding-bottom: 5%;
  display: flex;
  overflow-y: scroll;
}

#about-us-main-h1 {
  background-color: var(--fourth-color);
  width: 20%;
  min-height: 13%;
  color: var(--third-color);
  text-decoration: underline;
  text-decoration-color: var(--third-color);
  justify-content: center;
  align-items: center;
  display: flex;
}

.about-us-main-article {
  width: 95%;
  color: var(--third-color);
  font-size: 20px;
}

.about-us-main-article-h2 {
  background-color: var(--third-color);
  color: var(--fourth-color);
  justify-content: flex-start;
  align-items: center;
  height: 5%;
  padding: 1%;
  display: flex;
}

#about-us-main-article-h2-1 {
  width: 30%;
}

.about-us-main-article-p-a {
  color: var(--main-color);
  background-color: var(--fourth-color);
  text-decoration: none;
}

.about-us-main-article-p-a:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

#about-us-main-article-h2-2 {
  width: 50%;
}
